import Vue from "vue";
import VueResource from "vue-resource";
import NProgress from "nprogress";
import "nprogress/nprogress.css";

switch (window.location.hostname) {
  case "localhost":
    // window.API_ROOT = "http://localhost";
    window.API_ROOT = 'https://api.djoon.kz'
    break;
  case "djoon.kz":
    window.API_ROOT = "https://api.djoon.kz";
    break;
  default:
    window.API_ROOT = "https://api.djoon.kz";
}

Vue.use(VueResource);

Vue.http.options.crossOrigin = true;
Vue.http.options.xhr = { withCredentials: true };
Vue.http.options.root = API_ROOT;
// Vue.http.options.emulateJSON = true

Vue.http.options.progress = function (event) {
  if (event.lengthComputable) {
    NProgress.set(event.loaded / event.total);
  }
};

Vue.http.interceptors.push((request, next) => {
  NProgress.start();
  if (localStorage.getItem("session_id")) {
    request.headers.set("session-id", localStorage.getItem("session_id"));
  }

  if (localStorage.getItem("token")) {
    request.headers.set(
      "Authorization",
      "Bearer " + localStorage.getItem("token")
    );
  }
  next((response) => {
    NProgress.done();
    if (response.status > 400 && !response.body.need_verification) {
      Vue.toastr({
        description: response.body.message
          ? response.body.message
          : `Произошла ошибка, попробуйте позднее (Код: ${response.status})`,
        type: "error",
      });
    }
    if (response.status == 401) {
      localStorage.setItem("session_id", "");
      localStorage.setItem("token", "");
      localStorage.setItem("user", "");
      window.location = "/";
    }
    return response;
  });
});

